  font-family: 'Poppins';
  src:
    url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff2'),
    url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}
/* 2. Remove default margin */
* {
  margin: 0;
  padding: 0;
}
body {
  /* 3. Add accessible line-height */
  line-height: 1.5;
  /* 4. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}
/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
  border: none;
}
/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}
/* 8. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/* ACTUAL CSS */

body {
  display: flex;
  flex-direction: column;

  font-family: Poppins, sans-serif;
  font-size: 1rem;
  color: whitesmoke;
  background: #111;
  height: 100vh;
  width: 100vw;
}

header {
  display: flex;
  justify-content: center;

  background: #333;
  border-bottom: 1px solid whitesmoke;
}

nav {
  display: flex;
  justify-content: center;

  width: 100%;
  max-width: 1200px;
  padding-top: 40px;
  user-select: none;
}

nav h2 {
  transition-duration: 180ms;
  width: 100%;
  cursor: pointer;
}

.sub-nav-container {
  position: relative;
  width: 100%;
}

.sub-menu {
  display: flex;
  flex-direction: column;
  gap: 10px;

  position: absolute;
  top: 38px;
  left: -20px;

  background: #333;
  width: fit-content;
  padding: 10px 20px;
  visibility: hidden;
  border-bottom-right-radius: 0.8rem;
  border-bottom-left-radius: 0.8rem;
}

.visible {
  visibility: visible;
}

.sub-menu > p {
  text-shadow: 3px 3px 5px #111;
  cursor: pointer;
}

main {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.image-frame {
  position: relative;

  width: 1280px;
  height: 853px;
  overflow: hidden;
}

.image-container {
  display: flex;
  flex-wrap: nowrap;

  position: absolute;
  left: 0px;

  height: 100%;
  width: 6400px;
  z-index: -1;
}

.left-arrow,
.right-arrow {
  position: absolute;
  top: 50%;

  width: 50px;
  height: 50px;
  transform: rotateZ(45deg);
  cursor: pointer;
  transition-duration: 180ms;
}

.left-arrow {
  left: 40px;

  border-bottom: 2px solid whitesmoke;
  border-left: 2px solid whitesmoke;
}

.right-arrow {
  right: 40px;

  border-top: 2px solid whitesmoke;
  border-right: 2px solid whitesmoke;
}

.left-arrow:hover,
.right-arrow:hover {
  scale: 1.05;
}

.left-arrow:active,
.right-arrow:active {
  scale: 0.99;
}

img {
  user-select: none;
}

.nav-circles-container {
  display: flex;
  justify-content: center;
  gap: 15px;
  height: 20px;
}

.navigation-circles {
  height: 10px;
  width: 10px;
  border: 1px solid whitesmoke;
  border-radius: 100%;
  cursor: pointer;
  user-select: none;
}

.selected {
  background: whitesmoke;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 0.9rem;
  height: 5%;
}

a {
  color: inherit;
  text-decoration: underline;
}
